.menubtn {
  margin-top: 3px;
  /* z-index: 10; */
  width: 80px;
  /* height: fit-content; */
}

.menubtn:hover {
  /* margin: 20px; */

  transition: transform 0.8s ease-in-out;
  -webkit-animation: hoverAction 1.2s infinite;
          animation: hoverAction 1.2s infinite;
  /* transform: ; */
}
@-webkit-keyframes hoverAction {
  0% {
    transform: scale(1.01) rotate(5deg);
  }
  50% {
    transform: scale(1.01) rotate(-10deg);
  }
  100% {
    transform: scale(1.01) rotate(10deg);
  }
}
@keyframes hoverAction {
  0% {
    transform: scale(1.01) rotate(5deg);
  }
  50% {
    transform: scale(1.01) rotate(-10deg);
  }
  100% {
    transform: scale(1.01) rotate(10deg);
  }
}
.routerOption {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 20px;
  margin: 5px;
  width: 120px;
  height: 120px;
  background-color: rgba(0, 0, 0, 0.5);
  border-color: rosybrown;
  border-style: dotted;
  border-radius: 10px;
}
.routerOption :hover {
  transition: transform 0.1s ease-in-out;
  transform: scale(1.1);
}

.musicmenubtn > img {
